<template>
	<div
      class='messageframe-expression'
      v-if='expressionsSeen'
    >
		<div v-for='i in 50'
			:style='bgPosition(i)'
			:mark='i'
			@click = 'expressionToText'
		></div>


    </div>
</template>

<script>
	import {mapState,mapMutations} from 'vuex';



	export default{
		data(){
			return {
				great:{
					backgroundColor:'#222',
					
				}
			}
		},
		computed:{
			...mapState([
				'expressionsSeen',
			]),

		},


		methods:{
			bgPosition(i){
				return {
					backgroundPosition:'0px -'+i*30+'px',
				}
			},

		    expressionToText(e){
			  let t;
			  switch(parseInt(e.target.attributes[1].value)){
			    case 0:t = ''; break;
			    case 1:t = ''; break;
			    case 2:t = ''; break;
			    case 3:t = ''; break;
			    case 4:t = ''; break;
			    case 5:t = ''; break;
			    case 6:t = ''; break;
			    case 7:t = ''; break;
			    case 8:t = ''; break;
			    case 9:t = ''; break;
			    case 10:t = ''; break;
			    case 11:t = ''; break;
			    case 12:t = ''; break;
			    case 13:t = ''; break;
			    case 14:t = ''; break;
			    case 15:t = ''; break;
			    case 16:t = ''; break;
			    case 17:t = ''; break;
			    case 18:t = ''; break;
			    case 19:t = ''; break;
			    case 20:t = ''; break;
			    case 21:t = ''; break;
			    case 22:t = ''; break;
			    case 23:t = ''; break;
			    case 24:t = ''; break;
			    case 25:t = ''; break;
			    case 26:t = ''; break;
			    case 27:t = ''; break;
			    case 28:t = ''; break;
			    case 29:t = ''; break;
			    case 30:t = ''; break;
			    case 31:t = ''; break;
			    case 32:t = ''; break;
			    case 33:t = ''; break;
			    case 34:t = ''; break;
			    case 35:t = ''; break;
			    case 36:t = ''; break;
			    case 37:t = ''; break;
			    case 38:t = ''; break;
			    case 39:t = ''; break;
			    case 40:t = ''; break;
			    case 41:t = ''; break;
			    case 42:t = ''; break;
			    case 43:t = ''; break;
			    case 44:t = ''; break;
			    case 45:t = 'haha'; break;
			    case 46:t = ''; break;
			    case 47:t = ''; break;
			    case 48:t = ''; break;
			    case 49:t = 'ok'; break;
			  }
			  main.expressionSeen = false;
			  let input = document.getElementById('messageframe-input');
			  let end = input.selectionEnd;
			  let v = input.value;
			  input.value = v.substr(0,end) + "#("+t+")" + v.substr(end,v.length);
			},


			
		}

	}	
</script>

<style lang='less' scoped>
.messageframe-expression{
    background:rgba(255,255,255,0.5);
	position: absolute;
	width:520px;
	left:50%;
	bottom:50px;
	box-shadow: 0 0 10px #999;
	transform: translateX(-50%);
    z-index:100000000000000;
    @media(max-width: 468px){
    	width:90%;
    	height:130px;
    	overflow: scroll;

    }
    div{
		cursor:pointer;
		width: 30px;
		height: 30px;
		margin:5px;
		float: left;
		background: url('img/faces.png') no-repeat;
		background-position: left -30px;
		&:hover{
    		background-color:#EEF;
      	}
    }
  }
</style>